
.Main_banner {
	padding: 120px;
	justify-content: center;
	text-align: center;
	background-image: url("../../assets/img/background.jpg");
	background-color: rgba(0, 0, 0, 0.5);
	background-blend-mode: multiply;
	background-repeat: no-repeat;
	background-size: cover;
	margin-bottom: 30px;
}

.Main_banner * {
	margin-bottom: 20px;
}

.Main_banner h1 {
	font-size: 2rem;
}

.Main_banner form {
	display: flex;
	justify-content: center;
}

.Search_btn {
	margin-left: 10px;
	margin-top: 3px;
	width: 46px;
	height: 50px;
	padding: 0.6rem;
}

.Search_btn img {
	width: 100%;
	height: 100%;
}