		linear,
		left top, right top,
		from(var(--green)),
		color-stop(var(--green)),
		color-stop(var(--green)),
		color-stop(var(--green)),
		to(var(--green))
	);
	background-image: linear-gradient(
		to right,
		var(--green),
		var(--green),
		var(--green),
		var(--green),
		var(--green)
	);
	color: #fff;
}

footer.Inverse .green {
